import React, { FocusEventHandler, MouseEventHandler, useRef, useState } from 'react';
import { usePopper } from 'react-popper';

export interface TooltipProps {
  readonly title: string;
}

export const Tooltip: React.FC<TooltipProps> = ({ title, children }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [popperEl, setPopperEl] = useState(null);
  const [arrowEl, setArrowEl] = useState(null);
  const [isVisible, setIsVisible] = useState(false);

  const { styles, attributes } = usePopper(anchorEl, popperEl, {
    modifiers: [{ name: 'arrow', options: { element: arrowEl } }],
  });

  const leaveTimer = useRef(null);

  const handleMouseOver: MouseEventHandler = () => {
    clearTimeout(leaveTimer.current);
    setIsVisible(true);
  };

  const handleMouseLeave: MouseEventHandler = () => {
    clearTimeout(leaveTimer.current);
    leaveTimer.current = setTimeout(() => {
      setIsVisible(false);
    }, 800);
  };

  const handleFocus: FocusEventHandler = () => {
    clearTimeout(leaveTimer.current);
    setIsVisible(true);
  };

  const handleBlur: FocusEventHandler = () => {
    clearTimeout(leaveTimer.current);
    setIsVisible(false);
  };

  return (
    <>
      {React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
          return React.cloneElement(child, {
            ref: setAnchorEl,
            onMouseOver: handleMouseOver,
            onMouseLeave: handleMouseLeave,
            onFocus: handleFocus,
            onBlur: handleBlur,
          });
        }
        return child;
      })}
      {isVisible && (
        <div
          className="bg-overlay text-body1 text-xs rounded p-2"
          ref={setPopperEl}
          style={styles.popper}
          {...attributes.popper}
          onMouseOver={handleMouseOver}
          onMouseLeave={handleMouseLeave}
          onFocus={handleFocus}
          onBlur={handleBlur}
        >
          {title}
          <div className="bg-overlay" ref={setArrowEl} style={styles.arrow} />
        </div>
      )}
    </>
  );
};
